.App {
  text-align: center;
}


.header{
  display: flex;
  justify-content: space-evenly;
  border-bottom: 0.5rem solid black;
  
}

.header h1{
  font-size: 2.5rem;
  font-weight: 900;
  margin-bottom: 1%;
  margin-top: 6%;
  text-align: left;
}

.headerButton{
  display: flex;
  gap: 16px;

}
.headerButton button{
  background-color: black;
  font-size: 1.5rem;
  color: white;
  font-weight: 700;
  height: 10vh;
  margin-top: 2%;
  margin-bottom: 3%; 
}


.login h1, .addFriend h1{
  font-size: 6rem;
  font-weight: 900;
  margin-bottom: 1%;
}

.login form, .addFriend form{
  display: flex;
  flex-direction: column;
  width: 70%;
  margin: 0 auto;
}

label{
  font-size: 2rem;
  font-weight: 900;
  width: 70%;
  margin: 0 auto;
  text-align: left;
  max-width: 500px;
}

input, .login button, .addFriend button{
  background-color: black;
  color: white;
  width: 70%;
  max-width: 500px;
  font-weight: 600;
  height: 10vh;
  margin: 2% auto;
  font-size: 2rem;
}

.friendList h1{
  font-size: 6rem;
  font-weight: 900;
  margin-bottom: 1%;
}

.friendList{
  margin: 0 auto;
  width: 50%;
  font-size: 2rem;
  font-weight: 900;
  margin-bottom: 2%;
  text-align: left;
}